import React from 'react';
import { Link } from 'react-router-dom';
import './Home.css'

const Home = () => {
    return (
        <div className="rapper">
            <img src="./images/Logo.png" alt="Logo"/>

            <section className="container">
                <ul className="home">
                    <li>
                        <Link id="host" to={{
                            pathname: "/host"
                        }} className="HostButton"><img src="./images/Host.png" alt="Host"/></Link>
                    </li>
                    <li>
                        <Link to={{
                            pathname: "/client"
                        }} className="clientButton"><img src="./images/Client.png" alt="Client"/></Link>
                    </li>
                </ul>
            </section>
        </div>
    )
}

export default Home;